:root {
  // FONTS
  --font-title: "Poppins", sans-serif;
  --font-body: "Nunito", sans-serif;
  //COLOURS
  --primary-dark: #333;
  --secondary-dark: #444;
  --primary-light: #eee;
  --secondary-light: #ddd;
  --bg-hover: #ccc;
}
